const gallery = document.querySelector('.gallery');

function createGallery(images) {
  const imgArr = [];
  for (const { description, original, preview } of images) {
    const listItem = document.createElement('li');
    listItem.classList.add('gallery-item');

    const listLink = document.createElement('a');
    listLink.classList.add('gallery-link');
    listLink.href = original;

    const listImg = document.createElement('img');
    listImg.classList.add('gallery-image');
    listImg.src = preview;
    listImg.alt = description;

    listLink.appendChild(listImg);
    listItem.appendChild(listLink);
    imgArr.push(listItem);
  }
  gallery.append(...imgArr);
}

createGallery(images);

let galleryShow = new SimpleLightbox('.gallery-item .gallery-link', {
  captionSelector: 'img',
  captionsData: 'alt',
  captionDelay: 250,
});
galleryShow.on('show.simplelightbox', function () {});
